@use '../utils/forward' as *;

.products {
  &__container {
    padding-top: 121px;
    padding-bottom: 98px;
  }

  &__label {
    font-weight: 400px;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    display: block;
    text-transform: uppercase;
    font-family: 'Titan One';
    color: color-get('accent-secondary');
    margin-bottom: 15px;
  }

  &__headline {
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    letter-spacing: 4%;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Titan One';
    color: color-get('accent-primary');
  }

  &__list {
    list-style: none;
    margin-top: 144px;
    @include flex-display(wrap, 130px);
  }

  &__item {
    margin: 0 auto;
    border-radius: 24px;
    @include flex-basis(1, 130px);
  }

  &__item--1 {
    background-color: color-get('first-chld');
  }

  &__item--2 {
    background-color: color-get('second-chld');
  }

  &__item--3 {
    background-color: color-get('third-chld');
  }

  &__name {
    color: color-get('bg-secondary');
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 4%;
    text-transform: uppercase;
    position: relative;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 161px;
  }

  &__name::after {
    content: url(../images/dots.png);
    position: absolute;
    transform: translate(-260%, 170%);
  }
  &__name--3::after {
    content: url(../images/dots.png);
    position: absolute;
    transform: translate(-310%, 170%);
  }

  &__name--1::before {
    content: url(../images/products-icecream.png);
    position: absolute;
    transform: translate(-28%, -107%);
  }

  &__name--2::before {
    content: url(../images/products-coffee.png);
    position: absolute;
    transform: translate(-28%, -107%);
  }

  &__name--3::before {
    content: url(../images/products-milkshake.png);
    position: absolute;
    transform: translate(-22%, -109%);
  }

  &__description {
    text-align: center;
    padding: 30px 39px 111px 41px;
    padding-top: 66px;
    color: color-get('text-alternative');
    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.04em;
  }
}

@media screen and (min-width: 768px) {
  .products {
    min-width: 768px;
    max-width: 1199px;
    width: 100%;

    &__container {
      max-width: 1199px;
      margin: 0 auto;
      padding: 122px 34px 112px;
    }

    &__label {
      font-size: 26px;
      line-height: 30px;
    }

    &__headline {
      font-size: 48px;
      line-height: 55px;
      letter-spacing: 4%;
    }

    &__list {
      list-style: none;
      margin-top: 150px;
      @include flex-display(wrap, 20px);
    }

    &__item {
      @include flex-basis(3, 20px);
    }
  }
}

@media screen and (min-width: 1200px) {
  .products {
    min-width: 1200px;
    width: 100%;

    &__container {
      min-width: 1200px;
      margin: 0 auto;
      padding: 127px 112px 119px;
    }

    &__list {
      margin-top: 259px;
      @include flex-display(wrap, 30px);
    }

    &__item {
      @include flex-basis(3, 30px);
    }
  }
}
